.card {
  margin: 10px;
  display: inline-block;
  overflow: hidden;
  border: 2px solid green;
  width: 250px;
  height: 300px;
  color: blue;
}

.template {
  color: green;
  margin: 20px;
  border: 5px solid red;
  width: 250px;
  height: auto;
}

@media (max-width: 500px) {
  [class*="card"] {
    color: palevioletred;
  }

  .template {
    padding-left: 20px;
    border: 10px solid yellow;
  }
}

.cart {
  width: 400px;
  height: auto;
  margin-left: 100px;
  margin-top: 100px;
  border: 2px solid red;
  overflow: scroll;
  position: relative;
}

.image {
  width: 100px;
  height: 100px;
  margin-top: 10px;
  margin-left: 10px;
}

.data {
  margin-top: 2px;
}

