.container {
  display: flex;
  flex-direction: column;
  margin: 10px;
  background-color: #95dada;
  border: 1px solid grey;
  border-radius: 5px;
  cursor: pointer;
  transform: translateZ(0);
  transition: transform 0.25s ease-out;
}

.container:hover {
  transform: scale(1.05);
}

.mycard {
  margin: 5px 10px;
  height: 500px;
  width: 300px;
  border: 5px solid grey;
  border-radius: 5px;
  display: inline-block;
}

.text {
  text-align: center;
}

.myimage {
  height: 300px;
  width: 300px;
}
